import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { Document, Page, pdfjs } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';

const ResumeSection = () => {
    const resumeUrl = '/resume.pdf'; // Assuming the PDF is in the public folder

    // Set PDF.js worker URL
    pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

    return (
        <Box sx={{ textAlign: 'center', padding: '2rem' }}>
            <Typography variant="h4" gutterBottom>
                My Resume
            </Typography>
            <Typography variant="body1" paragraph>
                Below is a brief overview of my skills, experience, and qualifications. You can view and download my full resume using the options below.
            </Typography>
            {/* PDF Viewer */}
            <Box sx={{ width: '100%', height: '80vh', overflow: 'auto' }}>
                <Document
                    file={resumeUrl}
                    options={{ workerSrc: `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js` }}
                >
                    <Page pageNumber={1} />
                </Document>
            </Box>
            {/* Download Button */}
            <Button 
                variant="contained" 
                color="primary" 
                href={resumeUrl} 
                download="Gopala_Swamy_Mushini_Resume.pdf"
                sx={{ marginTop: '1rem' }}
            >
                Download Resume
            </Button>
        </Box>
    );
};

export default ResumeSection;
